body {
    font-family: sans-serif;
    margin: 0;
}
#root{
    min-height: 100vh;
    background-color: coral;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
}

.mark{
    color: aquamarine;
}

.card{
    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    border: 1px solid;
}

.box{
    display: grid;
    grid-template-columns: repeat(3,auto);
    gap: 10px;
}